@font-face {
    src: url('./Roboto-Regular.woff2') format('woff2'), url('./Roboto-Regular.woff') format('woff');
    font-family: 'Roboto';
    font-weight: normal;
    font-style: normal;
    font-display: swap;
  }
  
  @font-face {
    src: url('./Roboto-Medium.woff2') format('woff2'), url('./Roboto-Medium.woff') format('woff');
    font-family: 'Roboto';
    font-weight: 500;
    font-style: normal;
    font-display: swap;
  }
  
  @font-face {
    src: url('./Roboto-Bold.woff2') format('woff2'), url('./Roboto-Bold.woff') format('woff');
    font-family: 'Roboto';
    font-weight: bold;
    font-style: normal;
    font-display: swap;
  }